@import url(https://fonts.googleapis.com/css2?family=Ubuntu:wght@400;700&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Raleway&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Roboto+Condensed&display=swap);
body {
  background: #292929;
}

h1 {
  margin-top: 60px;
  color: antiquewhite;
}


header {
    background: #363636;
    border-top: 2px solid #f4811f;
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    z-index: 5050;
    /* background-color: var(--black-025); */
    transition: box-shadow cubic-bezier(.165, .84, .44, 1) .25s;
    height: 50px;
    font-family: Arial,"Helvetica Neue",Helvetica,sans-serif;
    box-sizing: border-box;
    color: white;
    font-family: 'Ubuntu', sans-serif;
}

.logo {
    width: 50px;
}

.label {
    position: absolute;
    top: 20px;
}

#log {
    position: absolute;
    right: 20px;
    top: 8px;
    background: #348bd4;
    color: white;
    font-family: 'Ubuntu', sans-serif;
    /* width: 70px; */
    font-weight: bold;
    padding: 8px;
    border: none;
    border-radius: 5px;
    outline: none;
}

#log:active {
   margin-top: 1px;
}

#log:hover {
    background: #0078cd;
}

input[type='search']{
    background: #292929;
    width: 500px;
    padding: .6em .7em;
    position: absolute;
    left: 220px;
    top: 7px;
    border: 1px solid grey;
    border-radius: 4px;
    color: rgb(197, 196, 196);
    outline-offset: 0px;
}
nav {
    position: absolute;
    top: 12px;
    left: 800px;
}

li {
    list-style-type: none;
    display: inline; 
}

a {
    margin-left: 20px;
    text-decoration: none;
    color: rgb(197, 196, 196);
}

a:hover {
    color: rgb(248, 248, 248);;
}

.profile {
    display: grid;
    grid-template-columns: 35px 100px;
    grid-template-rows: 15px 15px;
    grid-template-areas: "img login" "img rep";
    position: absolute;
    top: 10px;
    right: 100px;
}

.profile a {
    margin: 0;
}

#userImg {
    width: 30px; 
    grid-area: img;
}

 #userLogin {
    grid-area: login;
    font-size: 10px;
 
}

#userReputation {
    grid-area: rep;
    font-size: 10px;
}

/* @import url('https://fonts.googleapis.com/css2?family=Roboto+Condensed:wght@300&display=swap'); */

h1 {
    color: #e7e8eb;
    font-weight: 400;
    font-family: 'Raleway', sans-serif;
    font-size: 27px;
    margin-bottom: 40px;
    margin-left: 20px;
}

.mainContainer {
    display: grid;
    grid-template-columns: 15% 55% 30%;
    grid-template-areas: "filters questionDiv .";
}

.questionDiv {
    grid-area: questionDiv;
}

.filters {
    grid-area: filters;
    border-right: 1px solid #494949;
}

.question {
    width: 750px;
    height: -webkit-max-content;
    height: -moz-max-content;
    height: max-content;
    background: #363636;
    padding: 10px;
    padding-right: 20px;
    color: white;
    border-top: 1px solid #494949;
    display: grid;
    grid-template-columns: 200px 550px;
    grid-template-rows: auto auto;
    grid-template-areas: "allStats title" "allStats ." ;
    grid-gap: 10px;
}

.title {
    font-family: 'Raleway', sans-serif;
    color: #378ad3;
    font-size: 16px;
    font-weight: 600;
    margin: 0;
    grid-area: title;
}

.allStats {
    grid-area: allStats;
    display: flex;
    justify-content: space-around;
}

.statistic {
    text-align: center;
    width: 50px;
    height: 50px;
    color: #9fa6ad;
    font-family: 'Roboto Condensed', sans-serif;
    padding: 5px;
    font-size: 18px;
}

.statistic > p {
    margin: 10px 0;
    font-size: 12px;
}

.answered {
    border: 1px solid #60b171;
    color: #60b171;
    border-radius: 3px;
}

#checkAnswer {
    background: #5a9f70;
    color:azure;
}

.tagsAndOwner {
    display: flex;
    flex-wrap: wrap;
    /* padding: 0 5px; */
    /* justify-content: flex-start; */
}

.tagItem {
    /* flex: 1 1 auto; */
    margin: 5px 5px 0px 0px;
    background-color: #3c4951;
    color: #8cbfc1;
    padding: 7px;
    height: 10px;
    line-height: 10px;
    font-size: 15px;
    font-family: 'Roboto Condensed', sans-serif;
    border-radius: 3px;
}

.questionOwner {
    color: #9fa6ad;
    font-family: 'Roboto Condensed', sans-serif;
    font-size: 13px;
    /* text-align: end; */
    margin-top: 10px;
    margin-left: auto;
}

#userName {
    color: #378ad3;
}

/* filter block */

.filterH1 {
    margin-left: 40px;
}

.itemFilters, .ascDescFilters {
    font-family: 'Roboto Condensed', sans-serif;
    margin-left: auto;
    margin-right: 0;
    margin-top: 2px;
    padding: 5px;
    width: 150px;
}

.itemFilters {
    color: #dddddd;
    font-weight: 500;
    letter-spacing: 1px;
    background: #363636;
    border-right: 2px solid #f4811f;
    cursor: pointer;
}

.itemFilters::first-letter {
    text-transform: uppercase;
}

.itemFilters:hover {
    color: white; 
}

.ascDescFilters {
    border-left: 2px solid #f4811f;
    background: #363636;
    text-align: center;
    margin-bottom: 10px;
}

.ascDescFilters > button {
    margin-left: 5px;
    background: none;
    border: 1px solid #dddddd;
    color: #dddddd;
    cursor: pointer;
}

.ascDescFilters > button:hover {
    color: white;
    border: 1px solid white;
}
